// nav
.header__item--nav {
  position: relative;
  justify-self: flex-end;

  transform: translate(0, 0);

  @include mq(tablet) {
    z-index: 10;
    height: 100%;
    width: 90%;
    right: 0;

    margin-left: 10%;

    display: flex;
    justify-content: space-between;
    background-color: #c7c7c7;

    position: absolute;

    transform: translate(100%, 0);
  }

  @media (max-width: 500px) {
    width: 100%;
    align-items: flex-end;
  }
}
