@use "../../sass/variables.scss" as *;
@use "../../sass/mixins/media-query.scss" as *;

.main--catalog {
  gap: 1rem;
  height: 100%;
  position: relative;

  @include mq(small) {
    flex-direction: column;
  }
}

.dropdown {
  user-select: none;
  p {
    font-size: 1.6rem;
    font-family: "Bebas Neue", cursive;
  }

  li {
    padding-left: 1rem;
  }

  outline: 0.2rem solid var(--col-brand-main);
}

.dropdown__icon-continer {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--brand-base-color);
  clip-path: var(--clip-path);

  svg {
    fill: white;
  }
}

.dropdown-selected {
  display: flex;
  justify-content: space-between;
}

.dropdown-choice:hover {
  color: white;
  background-color: black;
}

.catalog__section {
  width: 100%;
  padding: 1rem;

  gap: 2rem;
  display: flex;
  flex-direction: column;
  height: 80vh;

  @include mq(tablet) {
    height: 75vh;
  }

  //catalog nav
  ul {
    display: flex;
    align-items: center;
    justify-content: space-between;

    li {
      // product type
      height: 2.5rem;
      display: flex;
      align-items: center;
      gap: 1rem;
      position: relative; //
    }

    li:last-child {
      // dropdown
      width: 18rem;
    }
  }

  box-shadow: 0 0 0.5rem 0.1rem rgb(228, 228, 228);
}

.catalog__section--products {
  overflow: scroll; // inner scroll
  overflow-x: hidden;

  height: 100%;
  padding: 1rem;

  gap: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  box-shadow: inset 0 0 1rem 0.1rem rgb(0, 0, 0, 0.2);
}

.catalog__product {
  aspect-ratio: 1 / 1;
  object-fit: cover;

  position: relative;
  height: max-content;
  margin-right: 0.75rem;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color: rgba(77, 77, 116);
  clip-path: polygon(100% 0, 100% 0%, 100% 100%, 0 100%, 0 0);

  img {
    padding: 1rem;
    flex: auto;
    width: 100%;
    height: auto;
  }

  a {
    padding: 0;
  }
}

.catalog__product--info {
  padding: 0.5rem;
  gap: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: gainsboro;

  h4,
  p {
    color: black;
    background-color: white;
    border-radius: 0.2rem;
    box-shadow: 0 0 0.2rem 0.05rem rgb(210, 210, 210);
  }

  h4 {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: inline-block; // https://stackoverflow.com/a/17783233
  }

  p {
    text-align: center;
    width: 30%;
  }
}

.product-name,
.product-price {
  padding: 0.2rem;
}

.product-name {
  letter-spacing: 0.2rem;
  font-family: "Bebas Neue", cursive;
}

.product-price {
  font-family: "Rajdhani", sans-serif;
}

.product-mask {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  background-color: whitesmoke;
}

.reveal-item {
  transition: 0.3s;
  background-color: transparent;
}

.clip-container-path {
  transition: 0.3s;
  clip-path: polygon(70% 0, 100% 15%, 100% 100%, 0 100%, 0 0);
}

//product-page | main view
.product-page {
  width: 100%;
  height: 35rem;
  display: flex;
}
